* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input{
  color:gray;
  width: 100%;
  font-size: 1.3rem;
}

.caja {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.cajaImg {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 10px;
  overflow: hidden;
}
.cajaImg p {
  padding: 7px;
  text-align: center;
}

.cajaImg img{
  max-height: 250px;
}
